"use client";

import React from "react";
import FishDetailModal from "./FishDetailModal";
import { Fish } from "@/lib/fishData";

interface FishCardProps {
  fish: Fish;
}

const FishCard: React.FC<FishCardProps> = ({ fish }) => {
  const getStockStatus = (stock: number) => {
    if (stock === 0) return { text: "Habis", color: "bg-red-500" };
    if (stock <= 5) return { text: "Stok Terbatas", color: "bg-yellow-500" };
    return { text: "Tersedia", color: "bg-green-500" };
  };

  const stockStatus = getStockStatus(fish.stock);

  return (
    <FishDetailModal fish={fish}>
      <div className="bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 border border-gray-100 cursor-pointer transform hover:scale-105">
        {/* Fish Image Placeholder */}
        <div className="h-48 bg-gradient-to-br from-blue-100 to-blue-200 flex items-center justify-center">
          <div className="text-center">
            <div className="w-16 h-16 bg-blue-300 rounded-full mx-auto mb-2 flex items-center justify-center">
              <span className="text-2xl text-blue-600">üêü</span>
            </div>
            <p className="text-sm text-blue-600 font-medium">Klik untuk detail</p>
          </div>
        </div>
        
        {/* Card Content */}
        <div className="p-4">
          <div className="flex justify-between items-start mb-2">
            <h3 className="text-lg font-semibold text-gray-900 font-serif">{fish.name}</h3>
            <span className={`px-2 py-1 rounded-full text-xs font-medium text-white ${stockStatus.color}`}>
              {stockStatus.text}
            </span>
          </div>
          
          <p className="text-sm text-gray-600 mb-3 line-clamp-2">{fish.description}</p>
          
          <div className="flex justify-between items-center">
            <span className="text-xl font-bold text-gray-900">{fish.price}</span>
            <span className="text-sm text-gray-500">
              Stok: <span className="font-medium">{fish.stock}</span>
            </span>
          </div>
          
          <div className="mt-3 text-center">
            <span className="text-xs text-blue-600 font-medium">Klik untuk detail ‚Üí</span>
          </div>
        </div>
      </div>
    </FishDetailModal>
  );
};

export default FishCard;
