"use client";

import React from "react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Fish } from "@/lib/fishData";

interface FishDetailModalProps {
  fish: Fish;
  children: React.ReactNode;
}

const FishDetailModal: React.FC<FishDetailModalProps> = ({ fish, children }) => {
  const getStockStatus = (stock: number) => {
    if (stock === 0) return { text: "Habis", color: "text-red-600", bgColor: "bg-red-100" };
    if (stock <= 5) return { text: "Stok Terbatas", color: "text-yellow-600", bgColor: "bg-yellow-100" };
    return { text: "Tersedia", color: "text-green-600", bgColor: "bg-green-100" };
  };

  const stockStatus = getStockStatus(fish.stock);

  return (
    <Dialog>
      <DialogTrigger asChild>
        {children}
      </DialogTrigger>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle className="text-2xl font-bold text-gray-900 font-serif">
            {fish.name}
          </DialogTitle>
          <DialogDescription className="text-base">
            Detail informasi ikan bakar pilihan
          </DialogDescription>
        </DialogHeader>
        
        <div className="space-y-4">
          {/* Image Placeholder */}
          <div className="w-full h-48 bg-gradient-to-br from-blue-100 to-blue-200 rounded-lg flex items-center justify-center">
            <div className="text-center">
              <div className="w-20 h-20 bg-blue-300 rounded-full mx-auto mb-3 flex items-center justify-center">
                <span className="text-3xl text-blue-600">üêü</span>
              </div>
              <p className="text-blue-600 font-medium">Foto {fish.name}</p>
            </div>
          </div>

          {/* Price */}
          <div className="text-center">
            <span className="text-3xl font-bold text-gray-900">{fish.price}</span>
          </div>

          {/* Stock Status */}
          <div className="text-center">
            <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${stockStatus.bgColor} ${stockStatus.color}`}>
              {stockStatus.text} - {fish.stock} ekor tersedia
            </span>
          </div>

          {/* Description */}
          <div className="bg-gray-50 rounded-lg p-4">
            <h4 className="font-semibold text-gray-900 mb-2">Deskripsi</h4>
            <p className="text-gray-700 leading-relaxed">
              {fish.description}
            </p>
          </div>

          {/* Additional Info */}
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div className="bg-gray-50 rounded-lg p-3">
              <h5 className="font-semibold text-gray-900 mb-1">Kategori</h5>
              <p className="text-gray-600">Ikan Bakar</p>
            </div>
            <div className="bg-gray-50 rounded-lg p-3">
              <h5 className="font-semibold text-gray-900 mb-1">Metode Masak</h5>
              <p className="text-gray-600">Dibakar langsung</p>
            </div>
          </div>

          {/* Order Info */}
          <div className="bg-blue-50 rounded-lg p-4">
            <h4 className="font-semibold text-blue-900 mb-2">Cara Pemesanan</h4>
            <p className="text-blue-700 text-sm">
              Hubungi kami melalui WhatsApp atau datang langsung ke tempat kami. 
              Ikan akan dimasak fresh sesuai pesanan Anda.
            </p>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default FishDetailModal;
